import React, { useState, useEffect } from "react";

// component
import { Container, Row, Col } from "react-bootstrap";
import { isMobile } from "react-device-detect";
import ReactPlayer from "react-player";
import { fetchData } from "../components/Functions/PageFunctions";

export default function PageGive() {
  const [dataList, setDataList] = useState([]);
  const [currentPage, setCurrentPage] = useState(0);
  const itemsPerPage = 1;
  const category = "sermon";

  const setAll = (dataList, currentPage) => {
    setDataList(dataList);
    setCurrentPage(currentPage);
  };

  useEffect(() => {
    fetchData(category, "", currentPage, itemsPerPage).then((getData) => {
      const result = getData.data.result.data[0];
      setAll(result, result.current_page);
    });
  }, [currentPage]);

  return (
    <>
      <Container fluid className="m-0 p-0 w-100">
        <Row
          lg={12}
          className={`page-divider m-0 p-0 ${isMobile ? "w-100" : "w-75"} h-25`}
        >
          <Col lg={12} className="h-100 align-items-center d-flex">
            <h1
              className={`page-divider-text p-5 ${
                isMobile ? "text-center" : ""
              }`}
            >
              Live Sermon
            </h1>
          </Col>
        </Row>
      </Container>
      <Container fluid className={`${isMobile ? "h-auto" : "h-100"} mt-5`}>
        <Row>
          <Col
            xs={12}
            lg={8}
            className="p-lg-5 mb-5 mb-lg-0"
            id={`${isMobile ? "" : "live-video-container"}`}
          >
            {dataList?.is_live === 1 ? (
              <ReactPlayer
                controls={true}
                url={dataList?.content_url}
                width="100%"
                height="auto"
                config={{
                  facebook: {
                    attributes: {
                      style: {},
                      "data-width": "auto",
                      "data-height": "auto",
                    },
                  },
                }}
              />
            ) : (
              <Container
                fluid
                className="no-live-background h-100 d-flex justify-content-center align-items-center"
              >
                <h1 className="no-live-wtbs-text">WTBS</h1>
                <p className="px-2 no-live-wtbs-p">is currently offline.</p>
              </Container>
            )}
          </Col>
          <Col xs={12} lg={4} className="d-flex align-items-center">
            <Container fluid className="h-auto">
              <Row>
                <Col xs={12} className="live-schedule-container">
                  <div>
                    <p>
                      <strong>Live Stream Schedule (Central time):</strong>
                    </p>

                    <ul>
                      <li>Sunday class 9:45 -10:45am</li>
                      <li>Sunday sermon 11:15 A.M. – 12:30pm</li>
                      <li>Wednesday class 7:00 – 8:00pm</li>
                      <li>
                        Friday Pastors’ Point of View 2:00pm (normally), from
                        Andy Woods Ministries Facebook page.
                      </li>
                    </ul>
                  </div>
                </Col>
                <Col xs={12} className="pt-5">
                  <div>
                    <p>
                      If you are in the greater Houston area, we encourage you
                      to come fellowship and worship with us! For those who
                      cannot physically attend our services, live streaming will
                      be available every Sunday morning and Wednesday evening.
                    </p>
                    <p>
                      The live stream will not include announcements or music.
                      The webcasts will then become part of our website’s sermon
                      database.
                    </p>
                    <p>
                      We hope this resource will help you grow in the grace and
                      knowledge of our Lord and Savior Jesus Christ (2 Peter
                      3:18).
                    </p>
                  </div>
                </Col>
              </Row>
            </Container>
          </Col>
        </Row>
      </Container>
    </>
  );
}
